.image-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.card-image {
    width: 300px;
    width: 100%;
    height: 300px;
    object-fit: cover;
    border-radius: 4px;
  }
  
  .card-date-time {
    font-size: 0.9rem;
    color: #666;
    margin-top: 8px;
  }
  
  .card-description {
    font-size: 1rem;
    margin-top: 8px;
    color: #333;
  }
  