.card {
    width: 300px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    text-align: center;
    margin: 20px;
  }
  .card-collection{
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    justify-content: center;
  }
  .card-gallery-div {
    position: relative;
    height: 200px;
    overflow: hidden;
  }
  
  .gallery-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity 0.5s ease;
  }
  
  .arrow {
    position: absolute;
    top: 50%;
    font-size: 24px;
    color: white;
    background: rgba(0, 0, 0, 0.5);
    padding: 8px;
    cursor: pointer;
    z-index: 1000;
    transform: translateY(-50%);
  }
  
  .prev {
    left: 10px;
  }
  
  .next {
    right: 10px;
  }
  
  .card-title {
    font-size: 1.5rem;
    color: #333;
    margin: 10px 0;
  }
  
  .trip-info {
    color: #777;
    font-size: 1rem;
    margin: 5px 0;
  }
  
  .places-list {
    list-style: none;
    padding: 0;
    margin: 10px 0;
  }
  
  .places-list li {
    font-size: 0.9rem;
    color: #555;
  }
  
  .price {
    font-weight: bold;
    font-size: 1.2rem;
    color: #d9534f;
    margin: 10px 0;
  }
  