.image-gallery {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    justify-content: center;
    margin: 20px;
  }
  
  /* Responsive layout for smaller screens */
  @media (max-width: 768px) {
    .image-gallery {
      flex-direction: column;
      align-items: center;
    }
  }
  